import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import SpeedDial from '@mui/material/SpeedDial';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import SaveIcon from '@mui/icons-material/Save';
import LaunchIcon from '@mui/icons-material/Launch';
import SearchIcon from '@mui/icons-material/Search';
import DeleteIcon from '@mui/icons-material/Delete';
import { theme } from '../../configtheme';
import { styled } from '@mui/material/styles';
import { useDispatch, useSelector } from 'react-redux';


const CustomSpeedDial = styled(SpeedDial)`
     
    & button{
        background-color:${theme.palette.valen.secondary};
        color:white !important
    }
    & button: hover{
        background-color:${theme.palette.valen.main}
    }

    & button.eliminar {
        background-color:${theme.palette.valen.error} 
    }

`;




export default function FloatButton({handleSave,handleSearch,handleDelete}) {

    const dispatch = useDispatch();
    const { save, delete: deleted, done, msje, search, newE, cancel, disabled } = useSelector(state => state.form);

    const [actions, setActions] = useState([])

    useEffect(() => {
        if (search) return setActions((oldarray)=>[...oldarray, { icon: <SearchIcon />, name: 'Buscar', click: handleSearch }])
        return setActions((prev) =>
            prev.filter(x => x.name !== 'Buscar')
        );
    }, [search])


    useEffect(() => {
        if (deleted) return setActions((oldarray)=>[...oldarray, { icon: <DeleteIcon /> ,className:"eliminar"  ,name: 'Eliminar',  click: handleDelete }])
        return setActions((prev) =>
            prev.filter(x => x.name !== 'Eliminar')
        );
    }, [deleted])


    useEffect(()=>{
        if (save) return setActions((oldarray)=>[...oldarray, { icon: <SaveIcon />  ,name: 'Guardar',  click: handleSave }])
        return setActions((prev) =>
            prev.filter(x => x.name !== 'Guardar')
        );
    },[save])



    return (
        <Box sx={{ transform: 'translateZ(0px)', flexGrow: 1 }}>
            <CustomSpeedDial
                ariaLabel="SpeedDial basic example"
                sx={{ position: 'fixed', bottom: 0, right: 0 }}
                icon={<LaunchIcon />}
            >
                {actions.map((action) => (
                    <SpeedDialAction
                        className={action.className}
                        id={action.name}
                        key={action.name}
                        icon={action.icon}
                        tooltipTitle={action.name}
                        onClick={action.click}
                    />
                ))}
            </CustomSpeedDial>
        </Box>
    );
}
